@charset "utf-8";

// Color system
$white:         #fff !default;
$black:         #000 !default;
$primary:       #000 !default;
$secondary:     #CCC !default;
$tertiary:      #EEE !default;
$quaternary:    #FFF !default;
$urgence:       #E52645 !default;

$theme-colors: () !default;
// stylelint-disable-next-line scss/dollar-variable-default
$theme-colors: map-merge(
  (
    "white":      $white,
    "black":      $black,
    "primary":    $primary,
    "secondary":  $secondary,
    "tertiary":   $tertiary,
    "quaternary": $quaternary,
    "urgence":    $urgence,
  ),
  $theme-colors
);


//Couleur du template
$backgroundColor: $white;


//Couleur des polices
$bodyColor: $primary;

// Police
$bodyFont: 'Montserrat', sans-serif;
$bodyFontSecondary: 'Josefin Sans', sans-serif;

// Positionning
$sidebarWidth: 400px;

//background specifique
$dateBg: url('http://via.placeholder.com/40x110/');
$urgenceBg: url('http://via.placeholder.com/40x110/FF0000');

@import 'bootstrap/bootstrap';
@import "main";
